@import 'nule.scss';
@import 'mixins.scss';

*{
   font-size: 15;
   font-family: 'Sansita Swashed', cursive;
   font-weight: 700;
}


html, body{
   height: 100vh; 
}

button, a{
   padding: 1.85vh 2vw;
   text-align: center;
   background: black;
   min-width: 170px;
   border: 3px solid white;
   color: white;
   text-transform: uppercase;
   border-radius: 15px;
   @include adaptiv-font(32,15);
   transition: transform 0.5s ease 0s;
}

a.active, button.active{
   border: 3px solid green;
   color: green;
   transition: all 0.8s ease 0s;
   box-shadow: 0 0 50px rgba(0,128,0,0.5);
}

a.active:hover, button.active:hover{
   transform: none;
}

button:hover{
   cursor: pointer;
   transform: scale(0.9);
}

@media (max-width: 960px){
   .wrapper{flex-direction: column;}
   .game__complex{flex-direction: column; order: 3;}
   .game__txt{ order: 1;}
   .game__grid{ order: 2; margin-bottom: 20px;}
   .game__complex button{margin-bottom: 15px;}
   .buttons_backgrd button{ margin: 20px 0;}
    body{  overflow: auto ; }
}
